<script>
export default {
  name: 'ContextMenuOriginal',
  created(){
    // 페이지 이동 시 스크롤 초기화.
    window.scrollTo(0, 0);
  },
  mounted () {

    const items = this.$refs.refWrapper.querySelectorAll('.item');
    const body = this.$refs.refContextMenuOriginal;

    body.addEventListener('click', function (e) {
      const targetClassList = e.target.classList;
      // 요소들 중 컨텍스트를 클릭을 했으면 어떤 동작도 하지 않아야 한다.
      if (targetClassList.contains('context')) {
        return;
      }
      // 요소들 중 아이템을 클릭을 했으면 아이템을 클릭을 했으면
      // 해당 아이템의 컨텍스트를 열거나 닫여야 하며,
      // 그 이외의 아이템들은 모두 다 닫혀야 한다.
      if (targetClassList.contains('item')) {
        targetClassList.toggle('open');
        items.forEach(function (elem) {
          if (elem !== e.target) elem.classList.remove('open');
        });
        return;
      }
      // 그 이외의 요소를 클릭을 했을 경우
      // 오픈 된 컨텍스트가 닫혀야 한다.
      items.forEach(function (elem) {
        elem.classList.remove('open');
      });
    });
  },

};
</script>

<style>

.context_menu_original{
    text-align: left;
}

.wrapper {
  border: 2px solid #000;
  padding: 10px;
}

.item {
  position: relative;
  border-top: 1px solid #000;
  margin: 0;
  padding: 10px;
}

.item:first-child {
  border-top: 0;
}

.item:after {
  position: absolute;
  bottom: 10px;
  right: 10px; 
  border: 1px solid #000;
  padding: 2px;
  font-size: 9px;
  background-color: #fff;
}

.item:hover:after {
  content: 'open';
}

.item.open:after {
  content: 'close';
}

.context {
  display: none;
  position: absolute;
  background-color: #fff;
  border: 1px solid #000;
  /* top: calc(100% - 10px); */
  top: calc(100% - 10px);
  right: 10px;
  width: 60%;
  padding: 10px;
  z-index: 1;
}

/* do something here! */
.item.open .context{
  display:block;
}
</style>