<script>
export default {
  name: 'ContextMenuHTMLDetails',
  created(){
    // 페이지 이동 시 스크롤 초기화.
    window.scrollTo(0, 0);
  },
  mounted () {
    const items = this.$refs.refWrapper.querySelectorAll('details');

    document.body.addEventListener('click', function (e) {
      if (e.target.nodeName !== 'SUMMARY' &&
    e.target.nodeName !== 'P') {
        items.forEach(function (item) {
          item.removeAttribute('open');
        });
      }
      items.forEach(function (item) {
        if (item !== e.target.parentElement) {
          item.removeAttribute('open');
        }
      });
    });
  },
  unmounted(){
    document.body.removeEventListener('click', function (e) {
      if (e.target.nodeName !== 'SUMMARY' &&
    e.target.nodeName !== 'P') {
        items.forEach(function (item) {
          item.removeAttribute('open');
        });
      }
      items.forEach(function (item) {
        if (item !== e.target.parentElement) {
          item.removeAttribute('open');
        }
      });
    });
  }
};
</script>

<style>

.wrapper {
    padding: 10px;
    border: 2px solid #000;
    text-align: left;
}

details {
  position: relative;
  margin: 0;
  border-top: 1px solid #000;
}

details:first-child {
  border-top: 0;
}

summary {
  padding: 10px;
  outline: 0;
}

summary::-webkit-details-marker {
  display: none;
}

details p {
  position: absolute;
  background-color: #fff;
  border: 1px solid #000;
  top: calc(100% - 10px);
  right: 10px;
  width: 60%;
  padding: 10px;
  margin: 0;
  display: none;
  z-index: 1;
}

.context-button {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

summary:after {
  position: absolute;
  bottom: 10px;
  right: 10px;
  border: 1px solid #000;
  padding: 2px;
  font-size: 9px;
  background-color: #fff;
}

summary:hover:after {
  content: 'open'
}

details[open] summary:after {
  content: 'close';
}

/* do something here */

details[open] p {
  display: block;
}
</style>